// ** Reactstrap Imports
import { Row, Col, Form, Button, Modal, Input, Label, ModalBody, ModalHeader } from 'reactstrap'
import { useForm, Controller } from 'react-hook-form'
import { selectThemeColors } from '@utils'
import { useEffect, useState } from 'react'
import { useParams, useHistory } from 'react-router-dom'
import {useDispatch, useSelector } from 'react-redux'
import Select from 'react-select'
import { toast, Slide } from 'react-toastify'
import { getSingleCategory, updateCategory } from './store/categorySlice'
const ToastContent = ({ name}) => (
  <>
    <div className='toastify-header'>
      <div className='title-wrapper'>
        <h6 className='toast-title fw-bold'>You have successfully updated {name.toUpperCase()}</h6>
      </div>
    </div>
  </>
)
const ToastError = ({ name}) => (
  <>
    <div className='toastify-header'>
      <div className='title-wrapper'>
        <h6 className='toast-title fw-bold'>Error Added {name.toUpperCase()}</h6>
      </div>
    </div>
  </>
)

const Edit = () => {
    // ** State
    const id = useParams()
    const dispatch = useDispatch()
    const store = useSelector(state => state.category)
    const [selectedStatus, setSelectedStatus] = useState(false)
    const history = useHistory()
      // ** Hook
    const {
    reset,
    control,
    setValue,
    setError,
    handleSubmit,
    formState: { errors }
    } = useForm({
      defaultValues: {
        category: ''
    }
    })
    const onSubmit = data => {
      if (Object.values(data).every(field => field.length > 0)) {
        dispatch(updateCategory({
          ...store.message,
          ...data, 
          status : selectedStatus.value
        })).then(res => {
          if (res.payload.error) {
            toast.error(
              <ToastError name={res.payload.error || res.payload.username || 'Category'} />,
              { icon: false, transition: Slide, hideProgressBar: true, autoClose: 2000 }
            )
          } else {
            history.push("/category")
            toast.success(
            <ToastContent name={res.payload.category || res.payload.username || 'Category'} />,
            { icon: false, transition: Slide, hideProgressBar: true, autoClose: 2000 }
          )
          }
        })
      } else {
        for (const key in data) {
          if (data[key].length === 0) {
            setError(key, {
              type: 'manual'
            })
          }
        }
      }
    }
    const statusOptions = [
      { value: true, label: 'Active' },
      { value: false, label: 'Inactive' }
    ]
    const handleReset = () => {
        reset({
            category: store.message.category
        })
      }

      useEffect(() => {
        dispatch(getSingleCategory(id.id)).then((res) => {
          setValue('category', res.payload.category) 
          setSelectedStatus(res.payload.status)  
        })
      }, [dispatch, setValue, id.id])

    return ( 
        <>
       <div className='text-center mb-2'>
            <h1 className='mb-1'>Edit Information</h1>
          </div>
          <Form onSubmit={handleSubmit(onSubmit)}>
            <Row className='gy-1 pt-75'>
            <Col md={6} xs={12}>
                <Label className='form-label' for='category'>
                  Category Name
                </Label>
                <Controller
                  control={control}
                  type='category'
                  id='category'
                  name='category'
                  render={({ field }) => (
                    <Input {...field} id='category' placeholder='Dress' invalid={errors.category && true} />
                  )}
                />
              </Col>
              <Col md={6} xs={12}>
                <Label className='form-label' for='status'>
                  Status:
                </Label>
                <Select
                  id='status'
                  isClearable={false}
                  className='react-select'
                  classNamePrefix='select'
                  options={statusOptions}
                  theme={selectThemeColors}
                  value={statusOptions[statusOptions.findIndex(i => i.value === selectedStatus)]}
                  onChange={option => setSelectedStatus(option)}
                />
              </Col>
              
              <Col xs={12} className='text-start mt-2 pt-50'>
                <Button type='submit' className='me-1' color='primary'>
                  Submit
                </Button>
                <Button
                  type='reset'
                  color='secondary'
                  outline
                  onClick={() => {
                    handleReset()
                    history.push("/category")
                  }}
                >
                  Discard
                </Button>
              </Col>
            </Row>
          </Form>
        </>
     )
}
 
export default Edit